import { element } from 'protractor';
import { AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Injectable, NgZone } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { Router } from '@angular/router';
import { User } from '../@core/data/domain/model';
import { auth } from 'firebase';

@Injectable({
  providedIn: 'root'
})
export class AuthenticationService {
  userData: any;

  constructor(
    public afStore: AngularFirestore,
    public ngFireAuth: AngularFireAuth,
    public router: Router,
    public ngZone: NgZone
  ) {
    this.ngFireAuth.authState.subscribe(user => {
      if (user) {
        this.userData = user;
        localStorage.setItem('user', JSON.stringify(this.userData));
        JSON.parse(localStorage.getItem('user'));
      } else {
        localStorage.setItem('user', null);
        JSON.parse(localStorage.getItem('user'));
      }
    });
  }

  signIn(email, phrase) {
    return this.ngFireAuth.signInWithEmailAndPassword(email, phrase);
  }

  registerUser(email, phrase) {
    return this.ngFireAuth.createUserWithEmailAndPassword(email, phrase);
  }

  sendVerificationMail() {
    this.ngFireAuth.currentUser.then( user => user.sendEmailVerification() )
    .then(() => {
      this.router.navigate(['verify-email']);
    });
  }

  phraseRecover(phraseResetEmail) {
    return this.ngFireAuth.sendPasswordResetEmail(phraseResetEmail)
    .then(() => {
      window.alert('已寄出重設密碼信件，請至信箱收取');
    }).catch((error) => {
      window.alert(error);
    });
  }

  get isLoggedIn(): boolean {
    const user: User = JSON.parse(localStorage.getItem('user'));
    return (user !== null && user.emailVerified !== false) ? true : false;
  }

  get isEmailVerified(): boolean {
    const user: User = JSON.parse(localStorage.getItem('user'));
    return (user.emailVerified === false ) ? false : true;
  }

  googleAuth() {
    return this.authLogin(new auth.GoogleAuthProvider());
  }

  authLogin(provider) {
    return this.ngFireAuth.signInWithPopup(provider)
    .then((result) => {
      this.ngZone.run(() => {
        this.router.navigate(['dashboard']);
      });
      this.setUserData(result.user);
    }).catch((error) => {
      window.alert(error);
    });
  }

  setUserData(user) {
    const userRef: AngularFirestoreDocument<User> = this.afStore.doc(`users/${user.uuid}`);
    const userData: User = {
      uuid: user.uuid,
      email: user.email,
      displayName: user.displayName,
      emailVerified: user.emailVerified
    };
    return userRef.set(userData, { merge: true });
  }

  signOut() {
    return this.ngFireAuth.signOut().then(() => {
      localStorage.removeItem('user');
      this.router.navigate(['login']);
    });
  }

}
